
import { Component, Input, OnDestroy, OnInit, Output, EventEmitter } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';

import { ConfirmationService, MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';
import { FileUpload } from 'primeng/fileupload';
import { saveAs } from 'file-saver';

import { ToastMessageService } from './../../../../services/toast/toast-message.service';
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../../constants/toast.constant';
import { NivelAprovacaoService } from './../../../../services/fluxo-pagamentos/niveis-aprovacao/nivel-aprovacao.service';
import { SolicitacaoService } from './../../../../services/fluxo-pagamentos/solicitacoes/solicitacao.service';
import { PatchSolicitacaoComprovanteDto, SolicitacaoDto } from './../../../../interfaces/fluxo-pagamentos/solicitacoes/solicitacao.dto';
import { SolicitacaoStatusEnum } from './../../../../constants/solicitacao-status.constant';
import { NivelAprovacaoDto } from './../../../../interfaces/fluxo-pagamentos/niveis-aprovacao/nivel-aprovacao.dto';

@Component({
  selector: 'app-solicitacao-detail',
  templateUrl: './solicitacao-detail.component.html',
  styleUrls: ['./solicitacao-detail.component.scss'],
})
export class SolicitacaoDetailComponent implements OnInit, OnDestroy {

  @Input()
  solicitacao: SolicitacaoDto;
  title: string = 'Solicitação';

  nivel_atual: NivelAprovacaoDto;

  hasUpdate = false;
  private subscriptions = new Subscription();

  constructor(
    private readonly modal: ModalController,
    private readonly toastMessageService: ToastMessageService,
    private readonly solicitacaoService: SolicitacaoService,
    private readonly nivelAprovacaoService: NivelAprovacaoService,
    private readonly confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    this.loadAprovadores();
  }

  ngOnDestroy(): void {
    this.confirmationService.close();
    this.subscriptions.unsubscribe();
  }

  downContaOriginal() {
    const { conta_original_url, conta_original } = this.solicitacao;
    saveAs(conta_original_url, conta_original);

    this.toastMessageService.presentToast({
      detalhe: 'Baixando a conta',
      duracao: ToastEnum.shortDuration,
      gravidade: ToastPrimeSeverityEnum.SUCESSO,
      titulo: 'Download em andamento'
    });
  }

  downComprovante() {
    const { comprovante_url, comprovante } = this.solicitacao;
    saveAs(comprovante_url, comprovante);

    this.toastMessageService.presentToast({
      detalhe: 'Baixando o comprovante',
      duracao: ToastEnum.shortDuration,
      gravidade: ToastPrimeSeverityEnum.SUCESSO,
      titulo: 'Download em andamento'
    });
  }

  seeSolicitacao() {

  }

  anexarComprovante(event, fileUpload: FileUpload) {
    const file = event.files[0];
    fileUpload.clear();
    const sub = this.solicitacaoService.createComprovante(file)
      .subscribe({
        next: (data) => {
          const { id: comprovante_id } = data;
          this.associarComprovante(this.solicitacao.id, { comprovante_id });
        },
      });
    this.subscriptions.add(sub);
  }

  associarComprovante(id: string, { comprovante_id }: PatchSolicitacaoComprovanteDto) {
    const sub = this.solicitacaoService.associarComprovante(id, { comprovante_id })
      .subscribe({
        next: (data) => {
          this.toastMessageService.presentToast({
            detalhe: `Arquivo anexado!`,
            duracao: ToastEnum.mediumDuration,
            gravidade: ToastPrimeSeverityEnum.SUCESSO,
            titulo: `Sucesso!`
          });
          this.solicitacao = data;
          this.hasUpdate = true;
        }
      });
    this.subscriptions.add(sub);
  }

  aprovar() {
    this.confirmationService.confirm({
      message: `Deseja prosseguir com a aprovação?`,
      header: `Confirme a aprovação`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const sub = this.solicitacaoService.aprovarSolicitacao(this.solicitacao.id)
          .subscribe({
            next: (data) => {
              this.toastMessageService.presentToast({
                detalhe: `Solicitação aprovada!`,
                duracao: ToastEnum.mediumDuration,
                gravidade: ToastPrimeSeverityEnum.SUCESSO,
                titulo: `Sucesso!`
              });
              this.hasUpdate = true;
              this.fechar();
            }
          });

        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastMessageService.clearToast();
      }
    });
  }
  rejeitar() {
    this.confirmationService.confirm({
      message: `Deseja prosseguir com a rejeição?`,
      header: `Confirme a rejeição`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {

        const sub = this.solicitacaoService.rejeitarSolicitacao(this.solicitacao.id)
          .subscribe({
            next: (data) => {
              this.toastMessageService.presentToast({
                detalhe: `Solicitação rejeitada!`,
                duracao: ToastEnum.mediumDuration,
                gravidade: ToastPrimeSeverityEnum.SUCESSO,
                titulo: `Sucesso!`
              });
              this.hasUpdate = true;
              this.fechar();
            }
          });

        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastMessageService.clearToast();
      }
    });
  }

  loadAprovadores() {
    if (this.solicitacao.status != SolicitacaoStatusEnum.PENDENTE) {
      return;
    }
    const sub = this.nivelAprovacaoService.findById(this.solicitacao.nivel_atual.id)
      .subscribe({
        next: (data) => {
          this.nivel_atual = data;
        }
      });

    this.subscriptions.add(sub);
  }

  async fechar() {
    await this.modal.dismiss(this.hasUpdate);
  }

}
