import { EventoAprovacaoDto } from './../../../../interfaces/fluxo-pagamentos/eventos-aprovacao/evento-aprovacao.dto';
import { PageOrder } from './../../../../constants/page.constant';
import { EventoAprovacaoService } from './../../../../services/fluxo-pagamentos/eventos-aprovacao/evento-aprovacao.service';
import { ToastMessageService } from './../../../../services/toast/toast-message.service';

import { Component, Input, OnDestroy, OnInit, Output, EventEmitter } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';

import { ConfirmationService, MenuItem } from 'primeng/api';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-evento-detail',
  templateUrl: './evento-detail.component.html',
  styleUrls: ['./evento-detail.component.scss'],
})
export class EventoDetailComponent implements OnInit, OnDestroy {

  @Input()
  solicitacao_id: string;

  eventos: EventoAprovacaoDto[] = [];
  selectedEvento: EventoAprovacaoDto;

  hasUpdate = false;
  private subscriptions = new Subscription();

  constructor(
    private readonly modal: ModalController,
    private readonly toastMessageService: ToastMessageService,
    private readonly eventoAprovacaoService: EventoAprovacaoService,
  ) { }

  ngOnInit() {
    this.loadEventosBySolicitacaoId();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadEventosBySolicitacaoId(page = 1) {
    this.eventoAprovacaoService.list(
      { solicitacao_id: this.solicitacao_id },
      { order: PageOrder.ASC, page, take: 50 }
    ).subscribe({
      next: (data) => {
        this.eventos = data.data;
      }
    });
  }

  selectEvento(evento: EventoAprovacaoDto) {
    this.selectedEvento = evento;
  }

  shouldGlow(evento: EventoAprovacaoDto) {
    if (!this.selectedEvento) {
      return false;
    }

    const result = this.selectedEvento.id == evento.id;
    return result;
  }

  async fechar() {
    await this.modal.dismiss(this.hasUpdate);
  }

}
