import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { NavigationExtras, Router } from '@angular/router';

import { ConfirmationService, MenuItem, SelectItem } from 'primeng/api';
import { Subscription, firstValueFrom } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';
import * as saveAs from 'file-saver';

import { ToastMessageService } from './../../../../services/toast/toast-message.service';
import { PageableDto } from './../../../../interfaces/others/pageable.dto';
import { ToastEnum, ToastPrimeSeverityEnum } from './../../../../constants/toast.constant';
import { PageOrder } from './../../../../constants/page.constant';
import { SolicitacaoDetailComponent } from '../solicitacao-detail/solicitacao-detail.component';
import { SolicitacaoService } from './../../../../services/fluxo-pagamentos/solicitacoes/solicitacao.service';
import { SolicitacaoDto } from './../../../../interfaces/fluxo-pagamentos/solicitacoes/solicitacao.dto';
import { SolicitacaoStatus, SolicitacaoStatusEnum } from './../../../../constants/solicitacao-status.constant';
import { EventoDetailComponent } from '../../eventos/evento-detail/evento-detail.component';

@Component({
  selector: 'app-solicitacao-main',
  templateUrl: './solicitacao-main.component.html',
  styleUrls: ['./solicitacao-main.component.scss'],
})
export class SolicitacaoMainComponent implements OnInit, OnDestroy {

  solicitacoes: PageableDto<SolicitacaoDto>;
  searchedSolicitacao = '';
  selectedSolicitacao: SolicitacaoDto;
  statusOptions: SelectItem[] = [];
  currentPage = 1;
  searchedOperacao = '';
  selectedStatus: SolicitacaoStatusEnum;

  editMenuItens: MenuItem[] = [{
    label: 'Opções',
    items: [
      {
        label: 'Detalhar',
        icon: 'pi pi-search',
        command: () => this.detalhar(this.selectedSolicitacao),
      },
      {
        label: 'Histórico',
        icon: 'pi pi-book',
        command: () => this.openHistorico(this.selectedSolicitacao),
      },
      {
        label: 'Notificar Aprovadores',
        icon: 'pi pi-bell',
        command: () => this.sendNotification(this.selectedSolicitacao),
      }
    ]
  }
  ];

  private subscriptions = new Subscription();

  constructor(
    private readonly modal: ModalController,
    private readonly toastMessageService: ToastMessageService,
    private readonly solicitacaoService: SolicitacaoService,
    private readonly confirmationService: ConfirmationService,
    private readonly router: Router,
  ) { }

  ngOnInit() {
    this.statusOptions = [...SolicitacaoStatus];
    this.selectedStatus = SolicitacaoStatusEnum.PENDENTE;
    this.loadSolicitacao();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadSolicitacao(page = 1, dropdown?: Dropdown) {
    const sub = this.solicitacaoService.list(
      {
        searchedOperacao: this.searchedOperacao,
        status: this.selectedStatus,
        searchedSolicitacao: this.searchedSolicitacao
      },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.solicitacoes = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }

  changeFiltro(event) {
    this.loadSolicitacao();
  }

  clearFilter() {
    this.loadSolicitacao(1);
  }

  search() {
    this.loadSolicitacao(1);
  }

  paginar(event) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentPage) {
      this.currentPage = page;
      this.loadSolicitacao(page);
    }
  }

  downContaOriginal({ conta_original_url, conta_original }: SolicitacaoDto) {
    saveAs(conta_original_url, conta_original);

    this.toastMessageService.presentToast({
      detalhe: 'Baixando a conta',
      duracao: ToastEnum.shortDuration,
      gravidade: ToastPrimeSeverityEnum.SUCESSO,
      titulo: 'Download em andamento'
    });
  }

  downComprovante({ comprovante_url, comprovante }: SolicitacaoDto) {
    saveAs(comprovante_url, comprovante);

    this.toastMessageService.presentToast({
      detalhe: 'Baixando o comprovante',
      duracao: ToastEnum.shortDuration,
      gravidade: ToastPrimeSeverityEnum.SUCESSO,
      titulo: 'Download em andamento'
    });
  }

  sendNotification({ id }: SolicitacaoDto) {
    this.confirmationService.confirm({
      message: `Deseja mesmo notificar?`,
      header: `O sistema já notifica automaticamente`,
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const sub = this.solicitacaoService.notificarAprovadores(id)
          .subscribe(response => {
            this.toastMessageService.presentToast({
              titulo: `Enviado!`,
              detalhe: `Os aprovadores serão notificados`,
              duracao: ToastEnum.shortDuration,
              gravidade: ToastPrimeSeverityEnum.INFORMACAO,
            });
          });

        this.subscriptions.add(sub);
      },
      reject: (type) => {
        this.toastMessageService.clearToast();
      }
    });



  }

  async createNew() {
    this.router.navigateByUrl('fluxo-pagamento');
  }

  detalhar(solicitacao: SolicitacaoDto) {
    this.showModal(SolicitacaoDetailComponent, { solicitacao }, ['modal-size-80']);
  }

  openHistorico({ id }: SolicitacaoDto) {
    this.showModal(EventoDetailComponent, { solicitacao_id: id }, ['modal-size-80', 'modal-transparent']);
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps,
    cssClass = ['modal-size-80'],
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass,
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate, role } = data;

        if (hasUpdate) {
          this.loadSolicitacao(this.currentPage);
          if (role == 'create') {
            this.toastMessageService.presentToast({
              detalhe: `Pagamento criado com sucesso!`,
              titulo: `Sucesso!`,
              duracao: ToastEnum.mediumDuration,
              gravidade: ToastPrimeSeverityEnum.SUCESSO,
            });
          }
        }

      });

    return await modal.present();
  }

}
